import debounce from 'lodash.debounce';
import service from './fetchCountries';
import countriesListTemplate from './templates/countries-list-items.hbs';
import countryTemplate from './templates/country-information.hbs';

const refs = {
  inputCountry: document.querySelector('#js-country-input'),
  ul: document.querySelector('#js-countries-list'),
  article: document.querySelector('#js-country-article'),
};

refs.inputCountry.addEventListener('input', debounce(inputHandler, 500));

function inputHandler(event) {
  let input = event.target.value;

  if (!input) {
    input = 'ukraine';
  }

  service.fetchCountries(input).then(countries => {
    createtMarkup(countries);
  });
}

function createCountriesListMarkup(items) {
  return countriesListTemplate(items);
}

function createCountryMarkup(items) {
  return countryTemplate(items);
}

function createtMarkup(countries) {
  const amountOfCountries = countries.length;

  refs.ul.innerHTML = '';
  refs.article.innerHTML = '';

  if (amountOfCountries >= 2 && amountOfCountries <= 10) {
    const countriesMarkup = createCountriesListMarkup(countries);

    refs.ul.insertAdjacentHTML('beforeend', countriesMarkup);
  }

  if (amountOfCountries === 1) {
    const countryMarkup = createCountryMarkup(countries);

    refs.article.insertAdjacentHTML('beforeend', countryMarkup);
  }

  if (amountOfCountries > 10) {
    console.log('Too many matches found. Please enter a more specific query!');
  }
}
